  export const disableAuditor = (data) => async dispatch => {
    try {
      dispatch(actions.setLoading(true))
  
      console.log("Data",data)
      let res = await API.statusTpi(data)
      if (res.data.code >= 400) throw new Error('Failed to change account status');
      swal({
        title: `Auditor ${data.disable==true ? "Disabled" : "Enabled"} Succesfully`,
        
        icon: "success",
        button: "Ok",
      })
      dispatch(actions.setLoading(false))
    } catch (err) {
  
      dispatch(actions.setLoading(false))
      swal({
        title: "Error",
        text: err.message,
        icon: "error",
        button: "Ok",
      })
      
      console.log(err)
    }
  }


