const DashboardNavbar = ({ onMobileNavOpen, ...rest }) => {
  const classes = useStyles();
  const matchAdani = useMatch('adani/observation');
  const matchContractor = useMatch('atgl/Contractor/Observation');
  const dateSelector = matchAdani || matchContractor;

  const { userData } = useSelector((state) => state.auth);
  const navigate = useNavigate();
  const [notifications] = useState([]);
  const dispatch = useDispatch();
  const { startDate, endDate } = useSelector((state) => state.header);

  const ColorButton = withStyles((theme) => ({
    root: {
      color: theme.palette.getContrastText('#F96E6E'),
      backgroundColor: '#F96E6E',
      '&:hover': {
        backgroundColor: '#F96E6E'
      }
    }
  }))(Button);

  return (
    <AppBar elevation={0} {...rest}>
      <Toolbar className={'d-flex w-100'}>
        <div className="d-flex">
          <IconButton color="inherit" onClick={onMobileNavOpen}>
            <MenuIcon />
          </IconButton>
          <RouterLink to={Constants.getUrl(userData?.type)}>
            {/* <Logo />OREN LOGO */}
            <img alt="" src={logo.avatarUrl} id="logo" />
          </RouterLink>
        </div>

        {/* <Box sx={{ flexGrow: 1 }} /> */}
        <div className="d-flex w-100 justify-content-end align-items-center">
          {dateSelector && (
            <>
              <form className={classes.container} noValidate>
                <label className="date-label">Start Date &nbsp;</label>
                <TextField
                  id="date"
                  type="date"
                  defaultValue={startDate}
                  //className={classes.textField}
                  onChange={(e) => dispatch(setStartDate(e.target.value))}
                  InputLabelProps={{
                    shrink: true
                  }}
                />
              </form>
              <form className={classes.container} noValidate>
                <label className="date-label">End Date &nbsp;</label>
                <TextField
                  id="date"
                  type="date"
                  defaultValue={endDate}
                  onChange={(e) => dispatch(setEndDate(e.target.value))}
                  style={{ padding: ' 5.5px 0px !important' }}
                  InputLabelProps={{
                    shrink: true
                  }}
                />
              </form>
            </>
          )}
          {/* <CustomerListToolbar /> */}
          <div className={'d-flex align-items-center'}>
            {/* <IconButton color="inherit">
              <Badge
                badgeContent={notifications.length}
                color="primary"
                variant="dot"
              >
                <NotificationsIcon />
              </Badge>
            </IconButton> */}
            <div className={classes.root} style={{ marginRight: '2rem' }}>
              <StyledBadge
                onClick={() => navigate('/profile')}
                overlap="circular"
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right'
                }}
                variant="dot"
              >
                <Avatar
                  alt="Remy Sharp"
                  src={userData?.profileImage?.fileLink ?? null}
                />
              </StyledBadge>
            </div>
            <ColorButton
              onClick={() =>
                dispatch(logOut(() => navigate('/', { replace: true })))
              }
              variant="contained"
              id="logout-btn"
              color="primary"
              className={classes.margin}
            >
              Log out
            </ColorButton>
            {/* <IconButton onClick={() => dispatch(logOut(() => navigate("/",{replace : true})))} color="inherit">
            <InputIcon />
          </IconButton> */}
          </div>
        </div>
        {/* <Hidden lgUp> */}

        {/* </Hidden> */}
      </Toolbar>
    </AppBar>
  );
};

